a {
  text-decoration: none;
}
.works_section {
    padding: 4rem;
    background: #fff;
  }
  
  .works_section h1 {
    text-align: center;
    font-size: 40px;
    font-family: sans-serif;
  }
  
  .works_container {
    display: flex;
    flex-flow: column;
    align-items: center;

    max-width: 1120px;

    width: 90%;
    margin: 0 auto;
  }
  
  .works_wrapper {
    position: relative;
    margin: 50px 0 45px;
  }
  
  .works_items {
    margin-bottom: 24px;
  }
  
  .works_item {
    display: flex;
    flex: 1;
    margin: 0 1rem;
    border-radius: 10px;
  }
  
  .works_item_link {
    display: flex;
    flex-flow: column;
    width: 100%;
    box-shadow: 0 6px 20px rgba(56, 125, 255, 0.17);
    -webkit-filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
    filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
    border-radius: 10px;
    overflow: hidden;
    text-decoration: none;
  }
  
  .works_item_pic-wrap {
    position: relative;
    width: 100%;
    /*padding-top: 67%;*/
    padding-top: 30%;
    overflow: hidden;
  }

  .works_item_img {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    display: block;
    width: 100%;
    max-width: 100%;
    height: 100%;
    max-height: 100%;
    object-fit: cover;
    transition: all 0.2s linear;
  }
  
  .works_item_img:hover {
    transform: scale(1.1);
  }
  
  .works_item_info {
    padding: 20px 30px 20px;
    /*height: 180px;*/
    height: 250px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
  
  .works_item_text {
    color: #252e48;
    
    font-family: sans-serif;
    font-weight: 500;
    
    font-size: 16px;
    line-height: 24px;
    /*width: 200px;*/ 
    width: 350px;
  }
  /***********/
